@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: 'Oswald', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/covid-19/static/media/virus.8a562f56.svg);
  background-size: 250px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  width: 100%;
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  box-shadow: 0 0 24px rgba(0,0,0,.15);
  min-height: 100vh;
  background-color: #fff;
}
#homeBanner .carousel-item img {
  width: 100%;
  }
  #homeBanner .carousel-indicators {
  bottom: -25px;
  margin-bottom: 0;
  }
  #homeBanner .carousel-indicators li {
  background-color: #bd61f2;
  }
  .list-box {
  border-radius: 5px;
  background-color: #4CAE46;
  background-image: url(/covid-19/static/media/bg-virus.86e2e0c8.svg);
  background-repeat: no-repeat;
  background-position: right;
  background-size: 100px;
  }
  .list-box img {
  margin-right: 10px;
  width: 60px;
  }
  .list-box span {
  display: block;
  font-size: 20px;
  }

  .list-box h4{
    font-weight: bold;
  }

  .bg-header{
    background-color: #6c3ccc;
    box-shadow:         0px 3px 5px 0px rgba(50, 50, 50, 0.3);
  }

  .bg-purple{
    margin-bottom: 15px;
    background-color: #bd61f2;
    
    color: #fff;
    box-shadow:         0px 3px 5px 0px rgba(50, 50, 50, 0.3);
  }
  
  .footer{
    background-color: #bd61f2;
    height: 6px;
  }

  .footer span{
    display: block;
    background-color: #6c3ccc;
    width: 80px;
    height: 6px;
  }
